import React, { useState, useEffect } from 'react'

const ComingSoon: React.FC = () => {
  const [timeLeft, setTimeLeft] = useState({
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0
  })

  useEffect(() => {
    // Set target date - adjust this to your desired launch date
    const targetDate = new Date('2025-11-01T00:00:00').getTime()

    const updateCountdown = () => {
      const now = new Date().getTime()
      const difference = targetDate - now

      if (difference > 0) {
        setTimeLeft({
          days: Math.floor(difference / (1000 * 60 * 60 * 24)),
          hours: Math.floor((difference % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)),
          minutes: Math.floor((difference % (1000 * 60 * 60)) / (1000 * 60)),
          seconds: Math.floor((difference % (1000 * 60)) / 1000)
        })
      }
    }

    updateCountdown()
    const interval = setInterval(updateCountdown, 1000)

    return () => clearInterval(interval)
  }, [])

  return (
    <section 
      className="section-padding bg-stone-200 relative overflow-hidden"
      aria-labelledby="coming-soon-heading"
    >
      {/* Background logo watermark */}
      <div className="absolute inset-0 flex items-center justify-center opacity-5">
        <img 
          src="/Logos/naturecredits_symbol.svg" 
          alt=""
          className="w-[600px] h-[600px] object-contain"
          aria-hidden="true"
        />
      </div>

      <div className="container-80 relative z-10">
        <div className="max-w-4xl ml-auto text-right">
          {/* Coming Soon Badge */}
          <div className="inline-block px-6 py-2 bg-[#C17A4A] text-white text-sm font-semibold mb-8 uppercase tracking-wide">
            COMING SOON
          </div>

          <h2 
            id="coming-soon-heading"
            className="text-3xl sm:text-4xl md:text-5xl lg:text-6xl font-bold mb-8 sm:mb-12 leading-tight"
          >
            <span className="text-stone-400">Our full website</span><br />
            <span className="text-stone-600">goes <span className="text-stone-800">LIVE</span> in:</span>
          </h2>

          {/* Countdown Timer */}
          <div className="flex justify-end gap-4 sm:gap-6 md:gap-8 mb-12 sm:mb-16">
            <div className="text-center">
              <div className="text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-bold text-[#5A7A6E] mb-2">
                {String(timeLeft.days).padStart(2, '0')}
              </div>
              <div className="text-sm sm:text-base text-stone-500 uppercase tracking-wide">
                Days
              </div>
            </div>
            <div className="text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-bold text-[#5A7A6E] self-start">
              :
            </div>
            <div className="text-center">
              <div className="text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-bold text-[#5A7A6E] mb-2">
                {String(timeLeft.hours).padStart(2, '0')}
              </div>
              <div className="text-sm sm:text-base text-stone-500 uppercase tracking-wide">
                Hours
              </div>
            </div>
            <div className="text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-bold text-[#5A7A6E] self-start">
              :
            </div>
            <div className="text-center">
              <div className="text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-bold text-[#5A7A6E] mb-2">
                {String(timeLeft.minutes).padStart(2, '0')}
              </div>
              <div className="text-sm sm:text-base text-stone-500 uppercase tracking-wide">
                Minutes
              </div>
            </div>
            <div className="text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-bold text-[#5A7A6E] self-start">
              :
            </div>
            <div className="text-center">
              <div className="text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-bold text-[#5A7A6E] mb-2">
                {String(timeLeft.seconds).padStart(2, '0')}
              </div>
              <div className="text-sm sm:text-base text-stone-500 uppercase tracking-wide">
                Seconds
              </div>
            </div>
          </div>

          <p className="text-lg sm:text-xl text-stone-500 leading-relaxed">
            Until then, we're already partnering with forest owners to bring local nature credits projects to 
            life and would love to hear from you.
          </p>
        </div>
      </div>
    </section>
  )
}

export default ComingSoon
