import React, { useState } from 'react'

const Contact: React.FC = () => {
  const [formData, setFormData] = useState({
    fullName: '',
    email: '',
    company: '',
    message: ''
  })

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    // Handle form submission
    console.log('Form submitted:', formData)
  }

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    })
  }

  return (
    <section 
      id="contact" 
      className="section-padding bg-[#4A6157] relative overflow-hidden"
      aria-labelledby="contact-heading"
    >
      {/* Background logo watermark */}
      <div className="absolute inset-0 flex items-center justify-center opacity-10">
        <img 
          src="/Logos/naturecredits_symbol.svg" 
          alt=""
          className="w-[800px] h-[800px] object-contain"
          aria-hidden="true"
        />
      </div>

      <div className="container-80 relative z-10">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 lg:gap-16 items-start">
          {/* Left Column - Text Content */}
          <div className="text-white">
            {/* Badge */}
            <div className="inline-block px-6 py-2 bg-[#C17A4A] text-white text-sm font-semibold mb-8 uppercase tracking-wide">
              CONNECT WITH US
            </div>

            <h2 
              id="contact-heading"
              className="text-3xl sm:text-4xl md:text-5xl lg:text-6xl font-bold mb-8 sm:mb-12 leading-tight"
            >
              Let's Grow Nature Credits Together
            </h2>
            
            <div className="space-y-4 text-lg sm:text-xl text-white/90 leading-relaxed mb-8">
              <p>
                Want to explore how your forest can generate nature credits and new revenue streams? 
                Interested in developing high-integrity biodiversity projects, exploring investment 
                opportunities, or collaborating with us?
              </p>
              <p className="text-[#C8D96F] font-semibold">
                Let's talk.
              </p>
            </div>
          </div>

          {/* Right Column - Contact Form */}
          <div>
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
                <div>
                  <input
                    type="text"
                    name="fullName"
                    placeholder="Full name"
                    value={formData.fullName}
                    onChange={handleChange}
                    className="w-full px-6 py-4 bg-transparent border border-white/30 text-white placeholder-white/60 rounded-lg focus:outline-none focus:border-[#C8D96F] transition-colors"
                    required
                  />
                </div>
                <div>
                  <input
                    type="email"
                    name="email"
                    placeholder="your@email.com"
                    value={formData.email}
                    onChange={handleChange}
                    className="w-full px-6 py-4 bg-transparent border border-white/30 text-white placeholder-white/60 rounded-lg focus:outline-none focus:border-[#C8D96F] transition-colors"
                    required
                  />
                </div>
              </div>

              <div>
                <input
                  type="text"
                  name="company"
                  placeholder="Your company name"
                  value={formData.company}
                  onChange={handleChange}
                  className="w-full px-6 py-4 bg-transparent border border-white/30 text-white placeholder-white/60 rounded-lg focus:outline-none focus:border-[#C8D96F] transition-colors"
                />
              </div>

              <div>
                <textarea
                  name="message"
                  placeholder="Tell us about your land and any questions you have..."
                  value={formData.message}
                  onChange={handleChange}
                  rows={6}
                  className="w-full px-6 py-4 bg-transparent border border-white/30 text-white placeholder-white/60 rounded-lg focus:outline-none focus:border-[#C8D96F] transition-colors resize-none"
                  required
                />
              </div>

              <button
                type="submit"
                className="w-full bg-[#C8D96F] hover:bg-[#B8C95F] text-stone-800 font-semibold py-4 px-8 rounded-lg text-lg transition-colors uppercase tracking-wide"
              >
                Send Message
              </button>
            </form>
          </div>
        </div>
      </div>
    </section>
  )
}

export default Contact